* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

body {
  display: flex;
  width: 100vw;
  height: 50vh;
  align-items: center;
  justify-content: center;
}
.ReactModal__Overlay{display:flex}.login-modal{font-family:'Nunito Sans', sans-serif;border-radius:0;margin:auto;width:440px;height:600px;box-shadow:0 8px 30px 0 rgba(0,0,0,0.2);background-color:rgba(255,255,255,0.85);background-position:center;background-size:cover;background-repeat:no-repeat;background-blend-mode:lighten;background-image:url("https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/71b2f647-19e8-4076-b8fa-f7f615e5c12a/de1cum3-41773e82-e08d-4971-82fa-e565705f67b7.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJzdWIiOiJ1cm46YXBwOiIsImlzcyI6InVybjphcHA6Iiwib2JqIjpbW3sicGF0aCI6IlwvZlwvNzFiMmY2NDctMTllOC00MDc2LWI4ZmEtZjdmNjE1ZTVjMTJhXC9kZTFjdW0zLTQxNzczZTgyLWUwOGQtNDk3MS04MmZhLWU1NjU3MDVmNjdiNy5qcGcifV1dLCJhdWQiOlsidXJuOnNlcnZpY2U6ZmlsZS5kb3dubG9hZCJdfQ.YHQWQuzBGeUPeh9tJZLcBZ8LkgnqQPXv2V-oActqirg");text-align:center}.login-modal .modal-header{margin:4rem 0 0;height:20%}.login-modal .logo h2{margin-top:0.75rem}.login-modal .modal-body{height:55%;padding:1rem 0}.login-modal .modal-body .extra-info{margin-top:.5rem;font-size:0.8rem;text-transform:uppercase}.login-modal .modal-body .extra-info .create-account-link{text-decoration:none;color:#3b66f5;font-weight:bold}.login-modal .modal-body .extra-info .create-account-link:hover{text-decoration:underline}.login-modal .modal-body .login-buttons{margin-top:1rem}.login-modal .modal-footer{height:calc(25% - 4rem);font-size:0.8rem;color:#83848f}.login-modal .modal-footer a{color:#83848f}.login-modal .modal-footer i{display:inline-block;width:0;overflow:hidden;text-indent:9999px;height:0.8em;margin:0 0.3em;vertical-align:middle;border-left:1px solid;font-size:16px}

.sign-in{width:300px;display:flex;flex-direction:column;margin:auto}.sign-in .title{margin:10px 0}.sign-in .buttons{display:flex;justify-content:space-between}

.group{position:relative}.group .form-input{background:none;background-color:white;font-size:16px;padding:10px 20px;display:block;width:100%;border:none;border-radius:2.5rem;border:1px solid #83848f;margin-top:1.5rem}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-17px;left:20px;font-size:14px;letter-spacing:.8px}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:#83848f;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:20px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-17px;left:20px;font-size:14px;letter-spacing:.8px}

.sign-in-button{width:100%;display:block;text-align:center;box-sizing:border-box;font-size:16px;line-height:24px;position:relative;display:inline-block;padding:5px 1em;transition:0.2s;color:#fff;border-radius:80px;text-align:center;white-space:nowrap;vertical-align:middle;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;border:none 0;text-decoration:none;background:#3b66f5 linear-gradient(45deg, #6da0fb 0, #3b66f5 100%);text-transform:lowercase;font-feature-settings:"c2sc";font-variant:small-caps;font-weight:500;margin-top:1rem}.sign-in-button:hover{text-decoration:none;opacity:.8;-mz-box-shadow:0 2px 8px rgba(0,0,0,0.3);box-shadow:0 2px 8px rgba(0,0,0,0.3)}.sign-in-button:active{transform:scale(0.98)}

.login-button{cursor:pointer;padding:0.75rem 2.5rem;border-radius:2.5rem;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;margin-left:1rem;border:2px solid;background-color:transparent;border-color:#8A4FFF;color:#8A4FFF}

.logo-container{height:100%;width:3rem;padding:.5rem;background-color:#fff;border-radius:50%;display:inline-block;margin:1rem 1.25rem;box-shadow:0 2px 10px rgba(31,33,41,0.3);transition:.2s}.logo-container:hover{opacity:.7}.logo-container:active{transform:scale(0.95)}.logo-container svg{width:100%;height:100%;display:flex;justify-content:center;align-items:center}

